import React, { useState } from 'react';
import Title from '../../common/Title';
import './styles.css';
import mita1 from '../../../pdf/figs/mita_map_1.PNG';
import mita2 from '../../../pdf/figs/mita_map_2.PNG';
import class1 from '../../../pdf/figs/class1.PNG';
import class2 from '../../../pdf/figs/class2.PNG';



const NiceFiguresTables = () => {
  const [abstractIsVisible, setAbstractIsVisible] = useState(false);
  return (
    <div id="nice-figures-tables">
      <Title> Nice Figures and Tables</Title>
      <span >Dell, Melissa (2010). "The Persistent Effects of Peru's Mining Mita". Econometrica.</span>
      <img src={mita1} alt="graph" />
      <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "The borders indicate the colonial institution of mita in a sub-region of Peru, which induced a forced labor system where indigenous communities had to send one-seventh of their adult male population to work in Potosí silver and Huancavelica mercury mines. Melissa's papers asks, first: what are the reduced-form effects of mita on contemporary local prosperity? The boundaries induce discrete change that allows for a sharp regression discontinuity design using with two running variables (latitude and longitude)." : ''}
      </p>
      <img src={mita2} alt="graph" />
      <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "OK! What can we learn about channels of institutional persistence? ... More broadly, the paper questions if inequality is a useful foundation for theories of long-run growth: historical inequality increased subsequent investments in public goods. ``The evidence in this study indicates that large landowners ---while they did not aim to promote economic prosperity for the masses--- did shield individuals from exploitation by a highly extractive state and ensure public goods. Thus, it is unclear whether the Peruvian masses would have been better off if initial land inequality had been lower, and it is doubtful that initial land inequality is the most useful foundation for a theory of long-run growth. (our emphasis, p. 1866)" : ''}
      </p>
      <span >Fredriksson, Peter, Öckert, Björn, Oosterbeek, Hessel. (2013). "The Persistent Effects of Peru's Mining Mita". Quarterly Journal of Economics.</span>
      <img src={class1} alt="graph" />
      <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "This article evaluates the long-term effects of class size in primary school using rich data from Sweden and exploiting variation in class size created by a maximum class size rule. I really like the way this table combines information on reduced-form, instrumental variables and first-stage estimates." : ''}
      </p>
      <img src={class2} alt="graph" />
      <p>
        <span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Comment{abstractIsVisible ? ': ' : " (click to expand)"}</span>
        {abstractIsVisible ? "." : ''}
      </p>
    </div>
    
  )
}

export default NiceFiguresTables;
